"use client";

import { CatalogIcon } from "@/assets/icons";
import { Button } from "../ui/button";
import { useState } from "react";

const CatalogMenu = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <div>
      <Button
        onClick={() => setIsMenuOpen(!isMenuOpen)}
        variant="outline"
        className="bg-cerulean w-[191px] h-10 flex items-center gap-[11px] text-white font-semibold text-sm leading-[18px] rounded-[9px] border-none hover:text-cerulean duration-300 ease-out"
      >
        <CatalogIcon />
        Каталог товаров
      </Button>
      {isMenuOpen && (
        <div className="bg-red-600 w-full h-screen mt-10">
                akdjalksjdakls
        </div>
      )}
    </div>
  );
};

export default CatalogMenu;
